@import 'index.scss';

.titleWrapper {
  position: relative;
}

.titleMain {
  font-weight: 300;
  font-size: 38px;
  line-height: 1.05;
  color: $white-color;
  max-width: 335px;
  background-color: transparent;
  margin-top: 90px;
  margin-bottom: 40px;
}

.spriteLine {
  transform: translate(-5px);
  width: 98px;
  height: 35px;
  stroke: $orange-secondary-color;
  fill: transparent;
}

.spanAccent {
  left: 0px;
  position: absolute;
}

@media (max-width: 347px) {
  .spriteLine {
    transform: translate(-15px, 3px);
    width: 98px;
    height: 35px;
    stroke: $orange-secondary-color;
    fill: transparent;
  }

  .spanAccent {
    top: 42px;
    left: 90px;
    position: absolute;
  }
}

@media screen and (min-width: $mobile-breakpoint) {
  .spriteLine {
    transform: translate(-5px);
    width: 98px;
    height: 35px;
    stroke: $orange-secondary-color;
    fill: transparent;
  }

  .spanAccent {
    left: 0px;
    position: absolute;
  }
}

@media screen and (min-width: $tablet-breakpoint) {
  .titleMain {
    font-size: 70px;
    line-height: 1.11;
    max-width: 600px;
    margin-top: 140px;
    margin-bottom: 64px;
  }

  .spriteLine {
    left: -25px;
    width: 185px;
    height: 67px;
  }
}

@media screen and (min-width: $desktop-breakpoint) {
  .titleMain {
    font-size: 70px;
    line-height: 1.11;
    max-width: 600px;
    margin-top: 151px;
  }
}
